<template lang="pug">
    .new__programme-content
        .programmes__title Учебная програма
        form.new__programme
            ul.new__programme-points
                li.form__item-programme
                    img(src='../../../assets/images/icons/ostrich.svg').img
                li.form__item-programme
                    label.label__name-programme Название
                        input(v-model='programme.name').new__programme__name
                li.form__item-programme
                    label.label__name-programme Предмет
                        select(@change='onChangeSubject()' v-model="selectedSubject").new__programme__subject
                            option(:value='0') Выбрать
                            option(:value='item.id' v-for='item in subjects') {{item.name}}
                li.form__item-programme
                    label.label__name-programme Группа
                        select(v-model='programme.group.id').new__programme__group
                            option(:value='0') Выбрать
                            option(:value='item.id' v-for='item in filteredGroups') {{item.code}}
            .buttons
                button(@click.prevent='saveProgramme').save__button Сохранить
                button(@click.prevent='resetProgramme').reset__button Отмена
            
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    created() {
        this.fetchTeacherGroups(this.user.id)
    },
    computed: {
        ...mapState ("teacherGroups", {
            teacherGroups: state => state.teacherGroups}),
        filteredGroups: function() {
            return this.teacherGroups
            //filter by subject
            .filter(ele=> this.selectedSubject == 0 || ele.subject.id == this.selectedSubject)
            //filter by group
            .filter(ele => this.programmes.filter(programme => programme.group.id == ele.id).length == 0)
        }
    },
    data() {
        return {
            selectedSubject: 0,
            programme: {
                name: '',
                group: {
                    id: 0
                },
                author: {
                    id: this.user.id
                }
            },
            errorOccur: false
        }
    },
    props: {
        subjects: Array,
        user: Object,
        programmes: Array
    },
    methods: {
        ...mapActions("teacherGroups", ["fetchTeacherGroups", "addProgramme"]),
        ...mapActions("programmes", ["addProgramme"]),
        ...mapActions("error", ["fetchError"]),
        saveProgramme() {
            if(this.programme.name=='' || this.programme.group.id==0 || this.programme.author.id==0){
                this.errorOccur = true
                this.fetchError('Не все поля заполнены!')
            }
            else{
                this.addProgramme(this.programme)
                this.$emit('eventNewProgramme')
            }
        },
        resetProgramme() {
            this.$emit('eventNewProgramme')
        },
        onChangeSubject() {
            this.programme.group.id = 0
        }
    }
}
</script>
